@use '/src/style/z-index-constant' as z;

%slide_btn {
  position: absolute;
  display: flex;
  align-items: center;
  cursor: pointer;
  top: 0;
  width: 24px;
  height: 100%;
  
  transition: var(--background-color-animation);
  z-index: z.$ui-index-3;
}

.feedback {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 90vh;
  
  &_container {
    position: relative;
    display: flex;
    box-sizing: border-box;

    padding: 25px;
    // border-radius: 25px;
    overflow: hidden;

    min-width: 250px;
    width: 100%;
    min-height: 450px;
  }

  &_prev {
    @extend %slide_btn;
    left: 0;
  }
  
  &_next {
    @extend %slide_btn;
    right: 0;
  }

  &_head {
    text-transform: uppercase;
  }
}

@layer bulletMain {
  .bullet {
    display: inline-block;
    
    background-color: currentColor;
    border-radius: 50%;
    opacity: 1;
    cursor: pointer;

    margin: 0 8px;
    width: 8px;
    height: 8px;
    transition: background-color 0.1s ease-out; 
  
    &_active {
      background-color: var(--second-on-color);
    }
  }
}

.pagination {
  &_horizontal {
    width: 100%;
    bottom: 0;
  }
}

@media (hover:hover) and (pointer: fine) {
  .feedback {
    &_prev,
    &_next {
      &:hover {
        background-color: var(--hovered);
      }
    }
  }
}